import Head from '../components/Head';
import Navbar from '../components/Navbar';
import BigVideo from '../components/BigVideo';
import { ParallaxProvider,ParralaxBanner } from 'react-scroll-parallax';
import About from '../components/About';
export default () => {
    return (
        <ParallaxProvider>
            <style global jsx>{`
                body {
                    margin: 0;
                    background:#eee;
                }
                h1 {
                    font-family: 'Barlow', sans-serif;
                }
             `}</style>
            <Head/>
            <Navbar />
            <ParralaxBanner
                layers={[
                    {
                        children: [<video autoPlay={true} muted={true} loop={true} className="bigvid">
                                <source src={"/static/Blue Sky Video_1.mp4"} type="video/mp4" />
                    </video>],
                        amount: 0.1,
                    },
                    {
                        children: [<video autoPlay={true} muted={true} loop={true} className="bigvid">
                                <source src={"/static/Blue Sky Video_1.mp4"} type="video/mp4" />
                    </video>],
                        amount: 0.2,
                    },
                ]}
                style={{
                    height: '500px',
                }}>
                <About />
            </ParralaxBanner>
            {/* <BigVideo videoSrc={"/static/Blue Sky Video_1.mp4"} index={0}/>
            
            <BigVideo videoSrc={"/static/Blue Sky Video_1.mp4"} index={-1}/> */}
        </ParallaxProvider>);
}
